
export const car = (state = {}, action={ type: null }) => {
    switch (action.type) {
        case "ADD_CAR":
            return {
                id: action.id,
                title: action.title,
                color: action.color,
                timestamp: action.timestamp,
                rating: 0
            }
        case "RATE_CAR":
            return (state.id !== action.id) ? state :
                {
                    ...state,
                    rating: action.rating
                }
        default :
            return state
    }
}

export const carsReducer = (state = [], action={ type: null }) => {
    switch (action.type) {
        case "ADD_CAR" :
            return [
                ...state,
                color({}, action)
            ]
        case "RATE_CAR" :
            return state.map(
                c => color(c, action)
            )
        case "REMOVE_CAR" :
            return state.filter(
                c => c.id !== action.id
            )
        default:
            return state
    }
}


